"use client";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { getAnalytics } from "firebase/analytics";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { sendEmailVerification } from "firebase/auth";
import { app } from "./api/firebase";


export default function Home() {
  const router = useRouter();
   const auth = getAuth(app);
  const analytics = getAnalytics(app);
  const handleLogin = async (e) => {
    e.preventDefault();
    const email = e.target.email.value;
    const password = e.target.password.value;
    try {
      await signInWithEmailAndPassword(auth, email, password);
      // router.push('/pages/dashboard'); // Перенаправление на дашбоард
      while (auth.currentUser) {
        if (!auth.currentUser.emailVerified) {
          await sendEmailVerification(auth.currentUser);
          window.alert('Пожалуйста, подтвердите ваш email. Ссылка для подтверждения отправлена на вашу почту.');
          await auth.signOut();
          router.push('/');
        } else {
          router.push('/pages/homeScreen'); // Перенаправление на домашний экран
        }
        break;
      }
    } catch (error) {
      window.alert('Ошибка входа: ' + error.message);
    }
  };

  return (
    <div className="flex min-h-screen flex-col items-center justify-between p-24">
      <div className="w-1/3 border-2 border-gray-300 rounded-xl shadow-lg">
      <h1 className="text-3xl font-semibold text-center rounded-t-xl p-4 bg-green-800 text-white  mb-10"> Geo-App</h1>
      <form onSubmit={handleLogin} className="flex flex-col space-y-4 p-4">
        <h2 className="text-lg font-medium">E-mail:</h2>
        <input
          type="email"
          placeholder="Электронная почта"
          className="border border-gray-300 p-2 rounded"
          id="email" name="email" required
        />
        <h2 className="text-lg font-medium">Пароль:</h2>
        <input
          type="password"
          placeholder="Пароль"
          className="border border-gray-300 p-2 rounded"
          id="password" name="password" required
        />
        <button
          type="submit"
          className="bg-green-800 text-white p-2 rounded hover:bg-green-700 mb-4"
        >
          Войти
        </button>
        <p className="text-sm text-center">When you log in, you accept our terms of service and privacy policy.</p>
        <p className="text-sm text-center">© 2025</p>
      </form>
      </div>
    </div>
  );
}
